import React from 'react';
import "../Css/DailyMeals.css";

import MealEntry from '../Components/MealEntry';
import FoodEntry from '../Components/FoodEntry';
import FoodItem from '../Components/FoodItem';
import Note from '../Components/Note';
import { app } from '../App';

class DailyMeals extends React.Component {
  constructor(props) {
    super(props);

    this.newFoodKey = "mustchange";
    this.state = {
      selectedDay: app.dateToStr(app.state.currentUser.access === "Guest" ? new Date("2020-06-07") : new Date()),//'2020-11-11')),
      dayEntry: {},
      dayFat: 0,
      dayCarbs: 0,
      dayProtein: 0,

      mealEntries: [],
      mealCounter: 0,
      selectedMeal: null,
      mealareaIsLoading: true,

      selectedFood: null,
      sFoodItems: [],
      sFoodCounter: 0,
      searchareaIsLoading: true,
      searchCounter: 0,
      amount: "",
      measure: "---",

      newFoodForm: false,
      newFoodItem: {
        foodname: "",
        brand: "",
        fat: 0, carbs: 0, protein: 0,
        price: 0,
        sizeinfo: 100,
        isdish: false,
        pic: "empty.png",
        userid: app.state.currentUser.userid,
        noteid: null,
        foodentries: null
      },

      selectedFoodDetails: null,
      composition: [],
      dishSelected: false
    };

    this.loadDailyMeals(this.state.selectedDay);
    this.loadSFoodItems("", false);
  }

  loadDailyMeals = (day) => {
    if (app.state.currentUser.access === "Guest" && (new Date(day) < new Date("2020-06-04") || new Date(day) > new Date("2020-06-11"))) {
      alert("As Guest user you can Only select from the 2020-06-04 to 2020-06-11 period!");
      return;
    }

    this.setState({
      mealEntries: [],
      selectedMeal: null,
      mealareaIsLoading: true,
      dayFat: 0,
      dayCarbs: 0,
      dayProtein: 0
    });

    ; (async () => {
      let { mealCounter } = this.state;

      let res = await fetch(app.getServerURL() + "/dailymeals", {
        method: "get",
        headers: {
          "content-type": "application/json",
          "reqdate": day,
          "userid": app.state.currentUser.userid,
        }
      });
      res = await res.json();

      const mealEntries = [];
      let first = true;
      for (let m of res.meals)
      if (!first)
      mealEntries.push(<MealEntry addToDay={this.addNewFoodEntryMacros} selectedChanged={this.onSelectedMealChanged} updateDayMF={this.onUpdateDay} removeMeal={this.onRemoveMeal} mealEntry={m} updateDay={this.updateSelectedMealNote} key={mealCounter++} />);
      else {
        mealEntries.push(<MealEntry signalSelect={true} addToDay={this.addNewFoodEntryMacros} selectedChanged={this.onSelectedMealChanged} updateDayMF={this.onUpdateDay} removeMeal={this.onRemoveMeal} mealEntry={m} updateDay={this.updateSelectedMealNote} key={mealCounter++} />);
        first = false;
      }

      this.setState({
        selectedDay: day,
        dayEntry: res,
        mealEntries: mealEntries,
        mealCounter: mealCounter,
        mealareaIsLoading: false,
      });
    })();
  };

  loadSFoodItems = (searchTerms, isAll) => {
    this.setState({
      sFoodItems: [],
      searchareaIsLoading: true,
      searchCounter: this.state.searchCounter + 1
    });
    const xx = this.state.searchCounter + 1;

    setTimeout(async (searchCounter = xx) => {
      if (searchCounter < this.state.searchCounter)
      return;

      let { sFoodCounter } = this.state;
      let res = await fetch(app.getServerURL() + "/dailymeals/foodsearch", {
        method: "get",
        headers: {
          "content-type": "application/json",
          "userid": app.state.currentUser.userid,
          "search": searchTerms,
          "isall": isAll
        }
      });
      res = await res.json();
      if (searchCounter < this.state.searchCounter)
      return;

      const sFoodItems = [];
      let first = true;
      for (let f of res)
      if (!first)
      sFoodItems.push(<FoodItem
        selectedChanged={this.onSelectedFoodChanged}
        foodItem={f}
        key={sFoodCounter++} />);
        else {
          sFoodItems.push(<FoodItem signalSelect={true} selectedChanged={this.onSelectedFoodChanged} foodItem={f} key={sFoodCounter++} />);
          first = false;
        }

        this.setState({
          selectedFood: first ? null : this.state.selectedFood,
          measure: first ? "---" : this.state.measure,
          sFoodItems: sFoodItems,
          sFoodCounter: sFoodCounter,
          searchareaIsLoading: false
        });
      }, 150);
    };

    onCommit = (ev) => {
      this.setState({
        searchareaIsLoading: true
      });
      ; (async (searchCounter) => {
        const { dayEntry, selectedDay } = this.state;

        const dayPutReq = dayEntry;
        dayPutReq.userid = app.state.currentUser.userid;
        dayPutReq.date = selectedDay;

        let res = await fetch(app.getServerURL() + "/dailymeals", {
          method: "put",
          headers: {
            "content-type": "application/json",
          },
          body: JSON.stringify(dayPutReq)
        });
        res = await res.json();

        this.loadDailyMeals(this.state.selectedDay);

        if (typeof res !== "string")
        alert(`Successfully entered date for day ${selectedDay}!\n --You can view resulting entry in the console`);
        else
        alert("There was an Error!");
        console.log(res);
      })();
      this.setState({
        searchareaIsLoading: false
      });
    };

    onAddNewMeal = (ev) => {
      if (app.state.currentUser.access === "Guest" && this.state.mealEntries.length === 5) {
        alert("As Guest user you cannot enter more than 5 Meals per day!");
        return;
      }

      const { mealEntries, dayEntry, newFoodForm } = this.state;
      let { mealCounter } = this.state;

      const newMPortion = document.querySelector(".newMPortion");
      if (isNaN(newMPortion.value)) {
        alert("Must Enter Valid Number for Portion!");
        newMPortion.value = "";
        return;
      }
      const newMName = document.querySelector(".newMName");
      const newMeal = {
        mealname: newMName.value === "" ? newMName.placeholder : newMName.value,
        portion: newMPortion.value === "" ? newMPortion.placeholder : newMPortion.value,
        noteid: null,
        foodentries: []
      };

      mealEntries.push(<MealEntry signalSelect={true} mealEntry={newMeal} addToDay={this.addNewFoodEntryMacros} selectedChanged={this.onSelectedMealChanged} removeMeal={this.onRemoveMeal} updateDayMF={this.onUpdateDay} updateDay={this.updateSelectedMealNote} key={mealCounter}/>);
      newMeal.key = mealCounter;
      dayEntry.meals.push(newMeal);

      newMName.value = "";
      newMPortion.value = "";
      this.setState({
        mealEntries: mealEntries,
        mealCounter: mealCounter + 1,
        dayEntry: dayEntry
      });
      if (!newFoodForm)
      document.querySelector(".search").select();
    };

    onRemoveMeal = (ev, sender) => {
      const { selectedMeal, mealEntries, dayEntry } = this.state;

      dayEntry.meals = dayEntry.meals.filter((m) => !((m.mealid && m.mealid === sender.state.mealEntry.mealid)
      || (m.key !== undefined && m.key.toString() === sender._reactInternalFiber.key)));

      this.setState({
        mealEntries: mealEntries.filter((meal) => meal.key !== sender._reactInternalFiber.key),
        dayEntry: dayEntry
      });
      this.addNewFoodEntryMacros(-sender.state.fat, -sender.state.carbs, -sender.state.protein);


      if (selectedMeal !== null && selectedMeal === sender)
      this.setState({
        selectedMeal: null
      });
      ev.stopPropagation();
    };

    onUpdateDay = (sender) => {
      const { dayEntry } = this.state;

      for (let m of dayEntry.meals)
      if ((m.mealid && sender.state.mealEntry.mealid === m.mealid)
      || (m.key !== undefined && sender._reactInternalFiber.key === m.key.toString())) {
        m.foodentries = sender.state.mealEntry.foodentries;
        break;
      }

      this.setState({
        dayEntry: dayEntry
      });
    };

    onSelectedMealChanged = (ev, sender) => {
      const { selectedMeal } = this.state;

      if (sender !== selectedMeal) {
        if (selectedMeal)
        selectedMeal.toggleHighlight();
        sender.toggleHighlight();

        this.setState({
          selectedMeal: sender,
          dishSelected: false
        });
      }
    };

    addNewFoodEntryMacros = (newfat, newcarb, newprotein) => {
      this.state.dayFat += newfat;
      this.state.dayCarbs += newcarb;
      this.state.dayProtein += newprotein;
      this.setState({});
    };

    onRemoveNote = () => {
      const { dayEntry } = this.state;

      dayEntry.note = null;
      this.setState({
        dayEntry: dayEntry
      });
    };

    updateSelectedMealNote = () => {
      const { dayEntry, selectedMeal } = this.state;

      for (let m of dayEntry.meals)
      if ((m.mealid && selectedMeal.state.mealEntry.mealid === m.mealid)
      || (m.key !== undefined && selectedMeal._reactInternalFiber.key === m.key.toString())) {
        m.note = selectedMeal.state.mealEntry.note;
        break;
      }

      this.setState({
        dayEntry: dayEntry
      });
    };

    onAddNewFoodEntry = (ev) => {
      const { selectedMeal, selectedFood, dayEntry, amount, measure, newFoodForm, newFoodItem, dishSelected, composition, selectedFoodDetails } = this.state;

      if (app.state.currentUser.access === "Guest" && (
        (selectedMeal && selectedMeal.state.foodEntries.length >= 7)
        || (dishSelected && selectedFoodDetails.foodentries.length >= 7))) {
          alert("As Guest user you cannot enter more than 7 Food Items per meal!");
          return;
        }

        if (selectedMeal === null && dishSelected === false) {
          alert("Must select a Meal or Dish!");
          return;
        }
        if (newFoodForm) {
          if (newFoodItem.foodname === "") {
            alert("New Food must have a Name !");
            return;
          }
          const sum = Number(newFoodItem.fat) + Number(newFoodItem.carbs) + Number(newFoodItem.protein);
          if (isNaN(sum) || (measure === "Grams" && (sum > 100 || sum <= 0))) {
            alert("Macro value are wrong!");
            return;
          }
          if (isNaN(newFoodItem.price)) {
            alert("Price value is wrong!");
            return;
          }
          if (isNaN(newFoodItem.sizeinfo) || newFoodItem.sizeinfo < 0) {
            alert("Piece Size value is wrong!");
            return;
          }
        }
        else if (selectedFood === null) {
          alert("Must select a Food Item !");
          return;
        }
        if (isNaN(amount)) {
          alert("Must Enter Valid Number for Amount!");
          this.setState({
            amount: ""
          });
          return;
        }

        const newFoodEntry = newFoodForm ? newFoodItem : selectedFood.state.foodItem;
        newFoodEntry.amount = amount ? amount : document.querySelector(".amountSize").placeholder;
        newFoodEntry.measure = measure;

        if (dishSelected) {
          let { sFoodCounter } = this.state;
          composition.push(<FoodEntry
            foodEntry={newFoodEntry}
            removeFoodEntry={this.onRemoveFoodEntry}
            key={sFoodCounter++} />);
            selectedFoodDetails.foodentries.push(newFoodEntry);

            this.setState({
              composition: composition,
              selectedFoodDetails: selectedFoodDetails,
              sFoodCounter: sFoodCounter,
              newFoodForm: false,
              newFoodItem: {
                foodname: "",
                brand: "",
                fat: 0, carbs: 0, protein: 0,
                price: 0,
                sizeinfo: 100,
                isdish: false,
                pic: "empty.png",
                userid: app.state.currentUser.userid,
                noteid: null,
                foodentries: null
              }
            });
          }

          else if (selectedMeal) {
            selectedMeal.addNewFoodEntry(ev, newFoodEntry);
            for (let m of dayEntry.meals)
            if ((m.mealid && selectedMeal.state.mealEntry.mealid === m.mealid)
            || (m.key !== undefined && selectedMeal._reactInternalFiber.key === m.key.toString())) {
              m.foodentries.push(newFoodEntry);
              break;
            }

            this.setState({
              dayEntry: dayEntry,
              newFoodForm: false,
              newFoodItem: {
                foodname: "",
                brand: "",
                fat: 0, carbs: 0, protein: 0,
                price: 0,
                sizeinfo: 100,
                isdish: false,
                pic: "empty.png",
                userid: app.state.currentUser.userid,
                noteid: null,
              }
            });
          }

          setTimeout(() => document.querySelector(".search").select(), 0);
        };

        onRemoveFoodEntry = (ev, sender) => {
          const { composition, selectedFoodDetails } = this.state;

          for (let i = 0; i < composition.length; i++)
          if (composition[i].key.toString() === sender._reactInternalFiber.key) {
            selectedFoodDetails.foodentries.splice(i, 1);
            composition.splice(i, 1)

            this.setState({
              composition: composition,
              selectedFoodDetails: selectedFoodDetails
            });
          }
        };

        saveDishIngr = (ev) => {
          const { selectedFoodDetails } = this.state;

          this.setState({
            selectedFoodDetails: null,
            dishSelected: false,
            searchareaIsLoading: true
          });
          ; (async () => {

            const dishData = {};
            dishData.dishid = selectedFoodDetails.foodid;
            dishData.foodentries = [];
            for (let f of selectedFoodDetails.foodentries)
            dishData.foodentries.push({ foodid: f.foodid, amount: f.amount, measure: f.measure });

            let res = await fetch(app.getServerURL() + "/dailymeals/dishupdate", {
              method: "post",
              headers: {
                "content-type": "application/json",
              },
              body: JSON.stringify(dishData)
            });
            res = await res.json();

            document.querySelector(".search").value = selectedFoodDetails.foodname + " " + selectedFoodDetails.brand;
            this.loadSFoodItems(selectedFoodDetails.foodname + " " + selectedFoodDetails.brand,
            document.querySelector("#isAll").checked);

            if (typeof res !== "string")
            alert(`Successfully saved dish: ${selectedFoodDetails.foodname} ${selectedFoodDetails.brand}!\n --You can view resulting entry in the console`);
            else
            alert("There was an Error!");
            console.log(res);
          })();
        };

        calculateValues = (ev) => {
          this.setState({
            selectedFoodDetails: null,
            dishSelected: false,
            searchareaIsLoading: true
          });
          ; (async () => {
            const { selectedFoodDetails } = this.state;

            let fat = 0, carbs = 0, protein = 0;
            for (let f of selectedFoodDetails.foodentries) {
              const { amount, measure, sizeinfo } = f;
              if (measure === "Pieces")
              if (sizeinfo === null) {
                fat += (f.fat * amount);
                carbs += (f.carbs * amount);
                protein += (f.protein * amount);
              }
              else {
                fat += (f.fat * amount * sizeinfo / 100);
                carbs += (f.carbs * amount * sizeinfo / 100);
                protein += (f.protein * amount * sizeinfo / 100);
              }
              else if (measure === "Grams") {
                fat += (f.fat * amount / 100);
                carbs += (f.carbs * amount / 100);
                protein += (f.protein * amount / 100);
              }
            }
            selectedFoodDetails.fat = Number(fat.toFixed(1));
            selectedFoodDetails.carbs = Number(carbs.toFixed(1));
            selectedFoodDetails.protein = Number(protein.toFixed(1));

            let res = await fetch(app.getServerURL() + "/yourfoods", {
              method: "post",
              headers: {
                "content-type": "application/json",
              },
              body: JSON.stringify(selectedFoodDetails)
            });
            res = await res.json();

            document.querySelector(".search").value = selectedFoodDetails.foodname + " " + selectedFoodDetails.brand;
            this.loadSFoodItems(selectedFoodDetails.foodname + " " + selectedFoodDetails.brand,
            document.querySelector("#isAll").checked);

            if (typeof res !== "string")
            alert(`Successfully calculated values for dish: ${selectedFoodDetails.foodname} ${selectedFoodDetails.brand}!\n --You can view resulting entry in the console`);
            else
            alert("There was an Error!");
            console.log(res);
          })();
        };

        onSelectedFoodChanged = (ev, sender) => {
          const { selectedFood, dishSelected } = this.state;

          if (sender !== selectedFood) {
            if (selectedFood)
            selectedFood.toggleSelected();
            sender.toggleSelected();
            if (document.activeElement !== document.querySelector(".search"))
            document.querySelector(".search").select();

            this.setState({
              selectedFood: sender,
              amount: "",
              measure: sender.state.foodItem.sizeinfo === null ? "Pieces" : "Grams",
              selectedFoodDetails: dishSelected ? this.state.selectedFoodDetails : null
            });

            if (!dishSelected) {
              ; (async () => {
                let { sFoodCounter, composition } = this.state;
                const { foodid, isdish, noteid } = sender.state.foodItem;
                let res = await fetch(app.getServerURL() + "/dailymeals/fooddetails", {
                  method: "get",
                  headers: {
                    "content-type": "application/json",
                    "foodid": foodid,
                    "isdish": isdish,
                    "noteid": noteid ? noteid : null
                  }
                });
                res = await res.json();

                composition = [];
                if (res.foodentries) {
                  for (let f of res.foodentries)
                  composition.push(<FoodEntry
                    foodEntry={f}
                    removeFoodEntry={this.onRemoveFoodEntry}
                    key={sFoodCounter++} />);
                  }

                  this.setState({
                    selectedFoodDetails: res,
                    sFoodCounter: sFoodCounter,
                    composition: composition
                  });
                })();
              }
            }
          };

          onDishSelect = (ev) => {
            const { dishSelected, selectedMeal, selectedFood, selectedFoodDetails } = this.state;

            if (!dishSelected) {
              if (selectedFood && selectedFood.state.foodItem.isdish) {
                const { foodname, brand, fat, carbs, protein, price, pic, isdish, note, measure } = selectedFood.state.foodItem;

                selectedFoodDetails.foodname = foodname;
                selectedFoodDetails.brand = brand;
                selectedFoodDetails.fat = fat;
                selectedFoodDetails.carbs = carbs;
                selectedFoodDetails.protein = protein;
                selectedFoodDetails.price = price;
                selectedFoodDetails.pic = pic;
                selectedFoodDetails.measure = measure;
                selectedFoodDetails.isdish = true;
                selectedFoodDetails.note = note;


                if (selectedMeal)
                selectedMeal.toggleHighlight();
                this.setState({
                  selectedMeal: null,
                  dishSelected: true
                });
              }
            }
          };

          onUpdateAttach = (newNote) => {
            const { dayEntry } = this.state;

            dayEntry.note = newNote;
            this.setState({
              dayEntry: dayEntry
            });
          };

          currentEntry = () => {
            const { selectedFood, amount, measure, newFoodForm, newFoodItem } = this.state;

            if (newFoodForm) {
              this.newFoodKey = this.newFoodKey === "mustchange" ? "willchange" : "mustchange";
              return <FoodEntry foodItem={newFoodItem}
                amount={amount}
                measure={measure}
                key={this.newFoodKey}
                readOnly="true" />
              }
              else
              if (selectedFood) {
                return <FoodEntry foodItem={selectedFood.state.foodItem}
                  amount={amount ? amount : (selectedFood ? (measure === "Pieces" ? 1
                  : selectedFood.state.foodItem.sizeinfo === 0 ? 100 : selectedFood.state.foodItem.sizeinfo) : 0)}
                  measure={measure}
                  key={selectedFood.state.foodItem.foodid.toString() + amount + measure}
                  readOnly="true" />;
                }
                else
                return <FoodEntry key={"F0"} readOnly="true" />
              };

              changeNewFoodValue = (field, value) => {
                const aux = this.state.newFoodItem;

                if (field === "per") {
                  const psi = document.querySelector(".PSInput");
                  if (value === "1Piece") {
                    psi.disabled = true;
                    psi.value = "";
                    aux.sizeinfo = null;
                    this.setState({
                      amount: 1
                    });
                  }
                  else {
                    document.querySelector(".PSInput").disabled = false;
                    psi.value = 100;
                    aux.sizeinfo = 100;
                    this.setState({
                      amount: 100
                    });
                  }
                }
                else {
                  if (field === "isdish")
                  aux["foodentries"] = value === true ? [] : null;
                  if (field === "sizeinfo")
                  value = value === "" ? null : value;
                  if (field === "fat" || field === "carbs" || field === "protein")
                  value = value === "" ? 0 : value;
                  aux[field] = value;
                }

                this.setState({
                  newFoodItem: aux,
                  measure: aux.sizeinfo === null ? "Pieces" : "Grams",
                });
              };

              onDayButtons = (ev, nrDays) => {
                const { selectedDay } = this.state;

                let d = new Date(selectedDay);
                d.setDate(d.getDate() + nrDays);

                this.loadDailyMeals(app.dateToStr(d));
              };

              onSearchKey = (ev) => {
                const { selectedFood, sFoodItems } = this.state;

                switch (ev.key) {
                  case "Enter":
                  this.onAddNewFoodEntry();
                  ev.preventDefault();
                  break;
                  case "Tab":
                  document.querySelector(".amountSize").select();
                  ev.preventDefault();
                  break;
                  case "Escape":
                  document.querySelector(".search").value = "";
                  ev.preventDefault();
                  break;
                  case "ArrowUp":
                  if (selectedFood) {
                    for (let i = 0; i < sFoodItems.length; i++)
                    if (sFoodItems[i].key === selectedFood._reactInternalFiber.key)
                    if (i === 0)
                    break;
                    else
                    document.querySelectorAll(".foodItem")[i - 1].click();
                  }
                  ev.preventDefault();
                  break;
                  case "ArrowDown":
                  if (selectedFood) {
                    for (let i = 0; i < sFoodItems.length; i++)
                    if (sFoodItems[i].key === selectedFood._reactInternalFiber.key)
                    if (i === sFoodItems.length - 1)
                    break;
                    else
                    document.querySelectorAll(".foodItem")[i + 1].click();
                  }
                  ev.preventDefault();
                  break;
                  default:
                  break;
                }
              };

              onAmountKey = (ev) => {
                switch (ev.key) {
                  case "Enter":
                  this.onAddNewFoodEntry();
                  ev.preventDefault();
                  break;
                  case "Tab":
                  document.querySelector(".search").select();
                  ev.preventDefault();
                  break;
                  case "Escape":
                  document.querySelector(".amountSize").value = "";
                  ev.preventDefault();
                  break;
                  case "ArrowUp":     //TODO:Change Measure??
                  console.log(ev.key);
                  ev.preventDefault();
                  break;
                  case "ArrowDown":   //TODO:Change Measure??
                  console.log(ev.key);
                  ev.preventDefault();
                  break;
                  default:
                  break;
                }
              };//TODO?

              render = () => {
                const { selectedDay, mealEntries, selectedFood, amount, measure, dayEntry, selectedFoodDetails, composition, mealareaIsLoading, searchareaIsLoading, sFoodItems, dayFat, dayCarbs, dayProtein, newFoodForm, dishSelected } = this.state;
                const { foodname, brand, fat, carbs, protein, price, pic } = dishSelected ?
                selectedFoodDetails
                : selectedFood ?
                selectedFood.state.foodItem
                : FoodItem.defaultFoodItem;

                return (
                  <main className="mainDailyMeals boxShow">
                    <div id="dayArea" className="subblock boxShow">
                      <div className="dayHeader">
                        <div className="datepick boxShow">
                          <label className="textHigh">Day: </label>
                          <button disabled={mealareaIsLoading} onClick={(ev) => this.onDayButtons(ev, -1)} className="ftButton" > {"<"}</button>
                          <input disabled={mealareaIsLoading} onChange={(ev) => this.loadDailyMeals(ev.currentTarget.value)} id="selectedDay" type="date" value={selectedDay} />
                          <button disabled={mealareaIsLoading} onClick={(ev) => this.onDayButtons(ev, 1)} className="ftButton">{">"}</button>
                        </div>
                        {mealareaIsLoading ? "LOADING..."
                        : <Note removeNote={this.onRemoveNote} note={dayEntry.note} key={"D" + (dayEntry.note ? dayEntry.note.noteid : "D0")} updateAttach={this.onUpdateAttach} />}
                      </div>
                      <div className="mealsArea">
                        {mealareaIsLoading ? "LOADING..." : mealEntries}
                      </div>
                      <hr />
                      <div className="dayTotal">
                        <span>Day Total:</span>
                        <span>{`${dayFat.toFixed(1)}||${dayCarbs.toFixed(1)}||${dayProtein.toFixed(1)}`}</span>
                      </div>
                      <button disabled={mealareaIsLoading} onClick={this.onCommit} className="ftButton">COMMIT DAY!</button>
                    </div>


                    <div id="addFoodArea" className="subblock boxShow">
                      {newFoodForm
                        ? <div className="newFoodForm">
                          <div className="newFoodHeader">
                            <label className="textHigh">Create New Food:</label>
                            <button onClick={() => this.setState({ newFoodForm: false })} className="ftButton">
                              +Select</button>
                            </div>
                            <span>Name:</span><input onChange={(ev) => this.changeNewFoodValue("foodname", ev.currentTarget.value)} placeholder="name" type="text" maxLength="50" />
                            <span>Brand:</span><input onChange={(ev) => this.changeNewFoodValue("brand", ev.currentTarget.value)} placeholder="brand" type="text" maxLength="50" />
                            <span>Macros:</span>
                            <div>
                              <input onChange={(ev) => this.changeNewFoodValue("fat", ev.currentTarget.value)} type="text" placeholder="0" maxLength="5" /><span>Fat</span>
                              <input onChange={(ev) => this.changeNewFoodValue("carbs", ev.currentTarget.value)} type="text" placeholder="0" maxLength="5" /><span>Carbs</span>
                              <input onChange={(ev) => this.changeNewFoodValue("protein", ev.currentTarget.value)} type="text" placeholder="0" maxLength="5" /><span>Protein</span>
                            </div>
                            <span>Per:</span>
                            <div>
                              <select onChange={(ev) => this.changeNewFoodValue("per", ev.currentTarget.value)} className="smallerInput">
                                <option>100Grams</option>
                                <option>1Piece</option>
                              </select>
                              <span>Piece Size:</span><input onChange={(ev) => this.changeNewFoodValue("sizeinfo", ev.currentTarget.value)} className="PSInput smallerInput" defaultValue="100" placeholder="null" type="text" maxLength="5" />
                            </div>
                            <span>Price:</span>
                            <div>
                              <input onChange={(ev) => this.changeNewFoodValue("price", ev.currentTarget.value)} className="smallerInput" placeholder="0" type="text" maxLength="10" />
                              <div><span>Is Dish?</span><input onChange={(ev) => this.changeNewFoodValue("isdish", ev.currentTarget.checked)} type="checkbox" /></div>
                            </div>
                            <label className="afaInfo">To add certain details (dish, composition, photo, note)or further edit foods go to Your Foods section.</label>
                          </div>

                          : [<div className="searchForm boxShow" key="searchForm">
                            <label className="textHigh">Search Food: </label>
                            <input className="isAll" onChange={(ev) => this.loadSFoodItems(document.querySelector(".search").value, ev.currentTarget.checked)} type="checkbox" maxLength="100" />
                              ALL
                              <button className="ftButton" onClick={() => this.setState({
                                newFoodForm: true,
                                selectedFood: null,
                                measure: "Grams",
                                amount: 100})}>
                              +New
                            </button>
                              <input className="search" onChange={(ev) => this.loadSFoodItems(ev.currentTarget.value, document.querySelector(".isAll").checked)} onKeyDown={this.onSearchKey} type="text" placeholder="search text" />
                              </div>,
                              <div className="searchResults boxShow" key="searchResults">
                                {searchareaIsLoading ? "LOADING..." : sFoodItems}
                              </div>]}
                              <div className="amountForm boxShow">
                                <label className="textHigh">Quantity: </label>
                                <input disabled={searchareaIsLoading} className="amountSize" type="text" value={amount}
                                  onChange={(ev) => this.setState({ amount: ev.currentTarget.value })}
                                  onKeyDown={this.onAmountKey}
                                  placeholder={selectedFood ? (measure === "Pieces" ? 1
                                  : selectedFood.state.foodItem.sizeinfo === 0 ? 100 : selectedFood.state.foodItem.sizeinfo) : 0} maxLength="10" />
                                  <select onChange={(ev) => this.setState({ measure: ev.currentTarget.value })} disabled={searchareaIsLoading} id="measureSelect" value={measure} >
                                    <option className={selectedFood ? (selectedFood.state.foodItem.sizeinfo === null ? "hidden" : "") : "hidden"}>
                                      Grams</option>
                                      <option className={selectedFood ? (selectedFood.state.foodItem.sizeinfo === 0 ? "hidden" : "") : "hidden"}>
                                        Pieces</option>
                                        <option className="hidden">---</option>
                                      </select>
                                    </div>
                                    <div className="searchEntry boxShow">
                                      <label className="textHigh lineDown">Current Entry:</label>
                                      {this.currentEntry()}
                                      <button disabled={searchareaIsLoading} onClick={this.onAddNewFoodEntry} className="ftButton">ADD FOOD ENTRY</button>
                                    </div>
                                    <hr />
                                    <div className="addMealArea">
                                      <div>
                                        <label className="textHigh">Meal Name:</label>
                                        <input className="newMName" type="text" placeholder={"Meal" + (mealEntries.length + 1)} />
                                        <label className="textHigh">Portion:</label>
                                        <input className="newMPortion" type="text" placeholder="1" maxLength="5" />
                                      </div>
                                      <button disabled={mealareaIsLoading} onClick={this.onAddNewMeal} className="newMeal ftButton">ADD NEW MEAL</button>
                                    </div>
                                  </div>

                                  {selectedFoodDetails ? (
                                    <div onClick={this.onDishSelect} id="foodDetailsArea" className={"subblock boxShow" + (dishSelected ? " highlight" : "")}>
                                      <div className="foodDetailsHeader">
                                        <div className="textHigh boxShow">{`${foodname} ${brand ? "@" + brand : ""}`}</div>
                                        {/*<Note removeNote={() => { }} note={selectedFoodDetails ? selectedFoodDetails.note : null}
                                        key={"F" + (selectedFoodDetails.note ? selectedFoodDetails.note.noteid : "0")}
                                      />*/}
                                    </div>
                                    <div className="foodPic boxShow">
                                      <img src={`FoodPics/${pic ? pic : "empty.png"}`} alt="[NO FOOD PIC]" />
                                    </div>
                                    <div className="foodInfo">
                                      <table>
                                        <thead>
                                          <tr><th>Name</th><td colSpan="2">{foodname}</td></tr>
                                          <tr><th>Brand</th><td colSpan="2">{brand ? brand : "--"}</td></tr>
                                          <tr><th>Macro</th><th>100g</th><th>1p</th></tr>
                                        </thead>
                                        <tbody>
                                          <tr><td>Fat</td>
                                          <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Grams" ? fat + "g" : "--"}</td>
                                          <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Pieces" ? fat + "g" : "--"}</td></tr>
                                          <tr><td>Carbs</td>
                                          <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Grams" ? carbs + "g" : "--"}</td>
                                          <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Pieces" ? carbs + "g" : "--"}</td></tr>
                                          <tr><td>Protein</td>
                                          <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Grams" ? protein + "g" : "--"}</td>
                                          <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Pieces" ? protein + "g" : "--"}</td></tr>
                                          <tr><td>Calories</td>
                                          <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Grams" ? (fat * 9 + protein * 4 + carbs * 4)
                                            .toFixed(1)
                                            + "Kc" : "--"}</td>
                                            <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Pieces" ? (fat * 9 + protein * 4 + carbs * 4)
                                              .toFixed(1)
                                              + "Kc" : "--"}</td></tr>
                                              <tr><td>Price</td>
                                              <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Grams" ? price + "Lei" : "--"}</td>
                                              <td>{(dishSelected ? selectedFoodDetails.measure : measure) === "Pieces" ? price + "Lei" : "--"}</td></tr>
                                            </tbody>
                                          </table>
                                          <div className="comp textHigh">{(selectedFood && selectedFood.state.foodItem.isdish) || dishSelected ? "Composition:" : ""}</div>
                                          <div className="foodEntries boxShow">
                                            {dishSelected || (selectedFood && selectedFood.state.foodItem.isdish) ? composition : ""}
                                          </div>
                                          <div className="tcenter">
                                            {dishSelected ? <button onClick={this.saveDishIngr} className="ftButton">SAVE DISH</button> : ""}
                                            {dishSelected ? <button onClick={this.calculateValues} className="ftButton">ADD VALUES</button> : ""}
                                          </div>
                                        </div>
                                      </div>
                                    ) : (
                                      <div id="foodDetailsArea" className="subblock boxShow">
                                        LOADING...
                                      </div>
                                    )
                                  }
                                </main >
                              );
                            };
                          }

                          export default DailyMeals;
